/* For Small phones */



/* For Bigger screens */
.app-container {
  background: #583672;
  max-width: 100%;
} 
.admin {
  padding-top: 100px;
  margin: 0;
  max-width: 100%;
  padding-bottom:100px;
}
.form-container{
  margin: auto;
  max-width: 70%;
  border: 3px solid transparent;
}
.login {
  padding: 80px 20px;
  background: #e7e2eb;
}
.login h2 {
  margin-left: 15%;
  color: #583672;
  padding-bottom: 20px;
}
.login input {
  margin-bottom: 20px;
  border-radius: 6px;
  width: 90%;
  border: 0 0 1 0;
  color: #583672;
}
.login button {
  margin-left: 25%;
  margin-top: 20%;
  padding: 5px 40px 5px 40px;
  border:0;
  color: #fff;
  background: #583672;
}
.welcome {
  text-align: center;
  padding: 200px 50px 200px 50px;
  color: #310938;
  background: linear-gradient(
      rgba(195, 78, 158, 0.45),
      rgba(200, 74, 141, 0.7),
      rgba(255, 0, 0, 0.45)
    ),url('https://res.cloudinary.com/kayode/image/upload/v1572935299/icons/manlaptop2_pl9owi.jpg') no-repeat;
  background-size: cover;
  
}
.welcome h2 {
  font-family: Pacifico;
}
.title {
  font-family: Pacifico;
}
header img {
  width: 40px;
  height: 40px;
}
header {
  color: #583672;
  border-bottom: 3px solid #fff;
  background: #f7f6f9;
}
.logo {
  padding-left: 80px;
}
.create-emp-container {
  padding: 40px;
  margin: auto;
  width: 90%;
  border: 3px solid transparent;
}
.form-input {
  margin: auto;
  padding: 50px;
}
.create-employee {
  padding-right: 40px;
  padding-left: 40px;
  background: #e7e2eb;
}
.create-employee .epm-input {
  width: 400px;
  margin-right: 10px;
}
.create-employee label {
  color:#583672;
}
.gender label {
  margin-right: 20px;
  padding-left: 5px;
}
.gender {
  background: #fff;
  width: 45%;
}
.gender input {
  margin-right: 5px;
}

.submit {
  width: 30%;
  margin-left: 25%;
  background: #583672;
  color: #fff;
  padding: 10px 10px;
  border: 0;
}

.emp-title {
  margin-left: 25%;
  color:#583672;
}
.article {
  padding: 20px;
  width: 80%;
  border: 1px solid blueviolet;
  background: #f7f6f9;
}
.user-header {
  font-weight: bold;
  padding-bottom: 15px;
}
.user-header .name-span {
 padding-left: 40px;
}

.date-span {
  float: right;
}
.user-header .prof_img {
  width: 25px;
  height: 25px;
  border-radius: 12px;
  border: 0;
}

.article .gif {
  width: 400px;
  height: 300px;
  margin-left: 3px;
}
.feed-list li {
  list-style-type: none;
}
.user-side {
  width: 150px;
  height: 150px;
  border-radius: 75px;
  border: 0;
}
.user-face {
  border: 1px solid #e7e2eb;
  padding-top: 40px;
  color:  #e7e2eb;
}
.user-face h3 {
  padding-left: 13px;
}
.post-btn {
  padding: 10px;
}
.post-btn .post-gif  {
  margin-left: 210px;
}
.post-btn .write-article {
  margin-left: 95px;
}
.post-btn button {
  color: #583672;
  font-size: 18px;
  font-weight: bold;
  padding: 10px;
}
.topStories {
  border: 1px solid #e7e2eb;
  color:  #e7e2eb;
  text-align: center;
}
.topStories ul li {
  list-style-type: none;
}
.article-body .article-title {
  text-align: center;
  color: #583672;
  font-weight: bolder;
}
.feed-list {
  margin-left: 50px;
}
.error {
  padding: 20%;
  text-align: center;
  font-size: 25px;
}
.article-form {
  padding: 10%;
}
.article-form form {
  margin-right: auto;
  margin-left: auto;
  width: 40%;
  background: #e7e2eb;
  padding: 30px;
}
.article-form form input {
  margin-bottom: 20px;
  width: 80%;
}
.article-form form textarea {
  margin-bottom: 20px;
  width: 80%;
}
.article-form form .article-btn {
  background: #310938;
  color: #fff;
  font-weight: bold;
  border: 0;
  padding: 10px 30px 10px 30px;
  margin-left: 120px;
}
.article-form form span {
  background: #310938;
  color: #fff;
  margin-right: 5px;
  font-size: 12px;
}
.comment-container ul li span {
  float: right;
}
.article-form h2 {
  color: #fff;
  text-align: center;
}

.One-Article {
  max-width: 60%;
  margin: auto;
  background: #e7e2eb;
  padding: 20px;
  color: #310938;
}
.One-Article img {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  border: 0;
}
.one-art-cont {
  padding: 10%;
}
.comment-container {
  max-width: 60%;
  margin: auto;
  background: #e7e2eb;
  padding: 20px;
  color: #310938;
}

.comment-container ul li {
  list-style-type: none;

}
.one-art-cont textarea {
  width: 55%;
  margin-left: 210px;
}
.one-art-cont button {
  padding: 10px 15px;
  background: #310938;
  color: white;
  border: 0;
  position: relative;
  bottom: 20px;
}
.one-art-body img {
  width: 400px;
  height: 400px;
  margin-left: 80px;
}
.one-art-header {
  border-bottom: 1px solid black;
  color: blue;
  justify-content: space-between;
}

@media only screen and (max-width: 500px) {
  .article-form {
  padding: 5%;
}
  .article-form form {
  margin-right: auto;
  margin-left: auto;
  width: 90%;
  background: #e7e2eb;
  padding: 15px;
}
.article-form form input {
  margin-bottom: 20px;
  width: 80%;
}
.article-form form textarea {
  margin-bottom: 20px;
  width: 80%;
}
.article-form form .article-btn {
  background: #310938;
  color: #fff;
  font-weight: bold;
  border: 0;
  padding: 10px 20px 10px 20px;
  margin-left: 50px;
}
.tag {
  width: 40px;
}
.One-Article {
  max-width: 90%;
  margin: auto;
}
.one-art-cont {
  padding: 0;
}
.comment-container {
  padding: 0;
  max-width: 90%;
}
.one-art-cont textarea {
  margin-left: 15px; 
  width: 74%;
  
}
.one-art-header {
  border-bottom: 1px solid black;
  color: blue;
}
.one-art-header span {
  float: right;
}
.one-art-header h6 {
  padding-right: 20px;
  padding-left: 10px;
}
.one-art-body img {
  width: 200px;
  height: 200px;
  margin-left: 20px;
}
.add-comment {
  padding: 14px 8px 14px 8px;
  position: relative;
  bottom: 20px;
  background: #310938;
  color: white;
  border: 0;
}
.comment-container ul {
  padding: 5px;
}
.comment-container ul li {
  margin: auto;
  border-bottom: 1px solid #310938;    
  color: brown;
}
.comment-container ul li span {
  float: right;
}
.commentHeader {
  color: blue;
}
.user-side {
  width: 150px;
  height: 150px;
  margin-left: 90px;
}
.user-face h3 {
  text-align: center;
}
.feed-list {
  padding: 0;
  width: 95%;
}
.feed-list .gif {
  width: 150px;
  height: 150px;
}
.write-article {
  position: relative;
  right: 100px;
}
.post-gif {
  position: relative;
  bottom: 50px;

}

}
@media screen and (max-width: 800px) and (min-width: 501px) {
  .one-art-cont textarea {
  margin-left: 120px;
  width: 50%;
  
}
  .add-comment {
  padding: 14px 8px 14px 8px;
  position: relative;
  bottom: 20px;
  background: #310938;
  color: white;
  border: 0;
}
.one-art-body img {
  width: 200px;
  height: 200px;
  margin-left: 40px;
}
}
@media screen and (max-width: 1024px) and (min-width: 801px) {
  .one-art-cont textarea {
  margin-left: 156px;
  width: 53%;
  
}
  .add-comment {
  padding: 14px 8px 14px 8px;
  position: relative;
  bottom: 20px;
  background: #310938;
  color: white;
  border: 0;
}
.one-art-body img {
  width: 200px;
  height: 200px;
  margin-left: 40px;
}
}